import img from "../../img/avatar.jpg";
import logo from "../../img/klinika-limfatek4.png"
import './header.css';

const Header = () => {
    var time = new Date();

    return (<div className="header">
        <img src={logo} alt="" className="header-img"/>

        <input type="text" className="header-search" placeholder={"Поиск..."}/>
        <div className="date">
            <div className="week">{day()}</div>
            <div className="dmy">{time.getDate()} {month()}, {time.getFullYear()}</div>
        </div>
        <div className="user">
            <div className="not"></div>
            <img src={img} alt="" className="photo"/>
            <div className="username">Ивенкина Мария</div>
        </div>
    </div>);
}

function day(){
    var time = new Date;
    switch (time.getDay())
    {
        case 0:
            return 'Понедельник';
            break;
        case 1:
            return 'Вторник';
            break;
        case 2:
            return 'Среда';
            break;
        case 3:
            return 'Четверг';
            break;
        case 4:
            return 'Пятница';
            break;
        case 5:
            return 'Суббота';
            break;
        case 6:
            return 'Воскресенье';
            break;
    }
}

function month(){
    var time = new Date;
    switch (time.getMonth())
    {
        case 0:
            return 'Января';
            break;
        case 1:
            return 'Февраля';
            break;
        case 2:
            return 'Марта';
            break;
        case 3:
            return 'Апреля';
            break;
        case 4:
            return 'Мая';
            break;
        case 5:
            return 'Июня';
            break;
        case 6:
            return 'Июля';
            break;
        case 7:
            return 'Августа';
            break;
        case 8:
            return 'Сентября';
            break;
        case 9:
            return 'Октября';
            break;
        case 10:
            return 'Ноября';
            break;
        case 11:
            return 'Декабря';
            break;
    }
}

export default Header;